<template>
  <div>
    <div id="leixin">
      <!-- <h3 class="title_container">库存汇总查询（汇总）</h3> -->
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="库存组织" class="organization" size="mini">
            <el-select clearable v-model="companyidList" multiple collapse-tags @focus="companyListInvok" :loading="paramsLoading" placeholder="请选择" filterable @change="companyChange">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <br>
          <el-form-item label="商品编号" size="mini">
            <el-input v-model="params.goods_id" clearable></el-input>
          </el-form-item>
          <el-form-item label="商品名称" size="mini">
            <el-input v-model="params.goods_name" clearable></el-input>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" @focus="warehouseInvok" :loading="loading" filterable placeholder="请选择">
              <el-option v-for="item in warehouseList" :key="item.warehouse_id" :label="item.warehouse_name" :value="item.warehouse_id">
                <span style="float: left">{{ item.warehouse_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="品牌" size="mini">
            <getBrand v-model='params.brand_id' />
          </el-form-item>
          <el-form-item label="类型" size="mini">
            <jybd-cascader ref="cascader" @cascaderChange="handleChange"></jybd-cascader>
          </el-form-item>
          <el-form-item label="工厂代码" size="mini">
            <el-input v-model="params.factory_code" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini">
            <!-- 默认显示库存不为0 传1显示库存为0	 -->
            <el-checkbox v-model="stocks" style="margin:0px 22px 0 62px">包含库存为0的商品</el-checkbox>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-dropdown style="margin:0 10px;" v-if="authority.b_export==1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportOrder(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportOrder(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <xTable v-if='tableColumn.length > 0' :ajax='{ url: "v2GetGoodsSummarizingList", params:{...this.params,stocks:stocks?1:0} }' :footerMethod='footerMethod' ref='xTable' :tableColumn='tableColumn' fieldUrl='v2/inventory/InventoryGoods/getGoodsSummarizingList' />
    </div>

  </div>
</template>
<script>
import { TableHead } from "./config/invertorysummer";
export default {
  name: "inventoryInventorysummer",
  mixins: [TableHead],
  data() {
    return {
      stocks: 0,
      loadingsum: true,
      TableDataSum: null,
      companyidList: [],
      loading: false,
      params: {
        export_type: 1,
        goods_type_id: "",
      },
      rowIndex: "",
    };
  },
  methods: {
    companyChange(val) {
      this.$set(this.params, "company_id", val.join());
      this.$set(this.params, "warehouse_id", ""); // 初始化仓库
      this.$set(this.params, "goods_name", ""); // 初始化商品名称
      this.$set(this.params, "goods_id", ""); // 商品编号初始化
      this.$set(this.params, "brand_id", ""); // 初始化品牌
      this.$set(this.params, "goods_type_id", ""); // 初始化品牌
    },
    exportOrder(scheduleType) {
      // 导出库存收发明细表导出URL
      let s = Object.assign(this.params, {
        export_type: scheduleType,
        stocks: this.stocks ? 1 : 0,
      });
      this.$axMethod.exportGoodsSummarizingUrl(s).then((res) => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    handleChange(val) {
      this.params.goods_type_id = val[2];
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.companyidList = [];
      this.$refs.cascader.clear();
      this.stocks = 0;
      this.onSearch();
    },
    portGetAlloodsSummarizingList() {
      this.$axMethod.getAlloodsSummarizingList(this.params).then(({ data }) => {
        if (data.code == 200) {
          this.TableDataSum = data.data;
          setTimeout(() => {
            this.$refs.xTable._proxy("updateFooter");
          }, 50);
        }
      });
    },
    onSearch() {
      this.TableDataSum = null;
      this.$refs.xTable._getData();
      this.portGetAlloodsSummarizingList();
    },
  },
  mounted() {
    this.portGetAlloodsSummarizingList();
  },
};
</script>

<style lang="less" scoped>
table {
  width: 100%;
  border: 1px solid #edf0f9;
}
.organization {
  width: 450px;
  /deep/.el-form-item__content .el-input {
    width: 340px;
  }
}
.table_sum {
  width: 100%;
  // overflow-x: auto;
  overflow: hidden;
  background: #f5f7fa;
}

.table_sum li {
  float: left;
  border: 1px solid #edf0f9;
  border-top: none;
  font-size: 12px;
  color: #1f2d3d;
  line-height: 23px;
  text-align: center;
  height: 23px;
  box-sizing: border-box;
  padding: 0px 10px;
}

table td {
  border-right: 1px solid #edf0f9;
  line-height: 23px;
  border-bottom: 1px solid #ebeef5;
  padding: 4px 10px;
}

table th {
  white-space: nowrap;
  overflow: hidden;
  background-color: #6aa8f3;
  color: #fff;
  font-weight: normal;
  line-height: 23px;
  padding: 4px 10px;
  text-align: left;
}
</style>