export const TableHead = {// 需要传进来一个this对象
  data() {
    return {
      tableColumn: [
        {
          type: "seq",
          title: '序号',
          width: 50
        },
        {
          title: '库存组织',
          field: 'company_name',
          width: 180
        },
        {

          title: '品牌',
          field: 'brand_name',
          width: 120
        },
        {
          title: '商品编号',
          field: 'goods_id',
          width: 110
        },
        {
          title: '商品名称',
          field: 'goods_name',
          width: 500
        },
        {
          title: '工厂代码',
          field: 'factory_code',
          width: 100
        },
        {
          title: '仓库名称',
          field: 'warehouse_name',
          width: 132
        },
        {
          title: '平均成本价',
          field: 'cost_price',
          width: 120,
          headerAlign: "right",
          align: "right",
          formatter: 'priceFormat'
        },
        {
          title: '销售价',
          field: 'sale_price',
          width: 120,
          headerAlign: "right",
          align: "right",
          formatter: 'priceFormat'
        },
        {
          title: '在库库存',
          field: 'stocks',
          headerAlign: "right",
          align: "right",
          width: 100,
        },
        {
          title: '本公司占用',
          field: 'company_occupy_num',
          headerAlign: "right",
          align: "right",
          width: 100,
        },
        // {
        //   title: '其他公司占用',
        //   field: 'other_company_occupy_num',
        //   headerAlign: "right",
        //   align: "right",
        //   width: 120
        // },
        {
          title: '可用库存',
          field: 'available_stocks',
          headerAlign: "right",
          align: "right",
          width: 120
        },
        {
          title: '不良品在库',
          field: 'incomplete_stocks',
          headerAlign: "right",
          align: "right",
          width: 120
        },
        {
          title: '成本金额',
          field: 'subtotal_amount',
          width: 120,
          headerAlign: "right",
          align: "right",
          formatter: 'priceFormat'
        }
      ]
    }
  },
  methods: {
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "页小计";
          }
          if (
            [
              "stocks",
              "available_stocks",
              "subtotal_amount",
              "incomplete_stocks"
            ].includes(column.property)
          ) {
            var num;
            if (this.$refs.xTable) {
              num = this.$utils.sum(
                this.$refs.xTable._proxy("getTableData").fullData,
                column.property
              );
            } else {
              num = 0;
            }
            var result = Number.isInteger(num) ? num : num.toFixed(2);
            return result;
          }
          return null;
        }),
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (
            [
              "stocks",
              "available_stocks",
              "subtotal_amount",
              "incomplete_stocks"
            ].includes(column.property)
          ) {
            var result;
            switch (column.property) {
              case "stocks":
                result = this.TableDataSum
                  ? this.TableDataSum.total_stocks
                  : "计算中...";
                break;
              case "available_stocks":
                result = this.TableDataSum
                  ? this.TableDataSum.available_stocks
                  : "计算中...";
                break;
              case "subtotal_amount":
                result = this.TableDataSum
                  ? this.$options.filters["priceFormatNone"](
                    this.TableDataSum.total_subtotal_amount
                  )
                  : "计算中...";
                break;
              case "incomplete_stocks":
                result = this.TableDataSum
                  ? this.$options.filters["priceFormatNone"](
                    this.TableDataSum.incomplete_stocks
                  )
                  : "计算中...";
                break;
            }
            return result;
          }
          return null;
        })
      ];
    }
  }
}

